<template>  
	<div class="main">
		<table class="highlight">
			<thead>
				<tr>
					<th><input type="checkbox" id="checkall" /><label for="checkall"></label></th>
					<th>ID</th>
					<th>title</th>
					<th>配置规则标题</th>
					<th>标签类型</th>
					<th>广告位标题</th>
					<th>广告源及权重</th>
					<th>发布线上时间</th>
					<th>从线上下线时间</th>
					<th>测试环境</th>
					<th>正式环境</th>
					<th>Action</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in items">
					<td><input type="checkbox" id="{{$index}}"/><label for="{{$index}}"></label></td>
					<td>{{item.id}}</td>
					<td>{{item.title}}</td>
					<td>{{item.rule_id.title}}</td>
					<td>{{{item.usertaglist}}}</td>
					<td>{{item.adpos.title}}</td>
					<td>{{{item.adgroup}}}</td>
					<td>{{item.last_release_ec2}}</td>
					<td>{{item.last_offline_ec2}}</td>
					<td>{{item.is_upload_local}}</td>
					<td>{{item.is_upload_ec2}}</td>
					<td><a class="waves-effect waves-light btn">ViewDetail</a></td>
				</tr>
			</tbody>
		</table>
	</div>
</template> 
<script>
import adpushlist from '../adpush/adpush.js'

var API_URL = 'http://172.16.7.101:8080';
var LIST_URL= API_URL + '/turbot_admin/adpush/list?index=1&limit=20';
export default {
	data: function(){
		return {
			 items: []
		}
	},
	ready: function() {
		var _this = this;
		adpushlist.listData().then(function(data){
			console.log(data.data)
			_this.items = data.data.items;
		})
		
	}
}

</script>




