import api from '../../common/utils/api.js'

var LIST_URL= api.API_URL + '/turbot_admin/adpush/list?index=1&limit=20';
export default {

		listData(){
			var data = $.ajax({
	            url: LIST_URL,
	            type: 'GET'
	        }).done(function(){
	        	console.log('done');
	        })

	        return data;
		}


}



